@import 'normalize.css';
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');

:root {
    --primary: #f00;
    //DefaultLayout
    --default-layout-header-height: 56px;
    --white-color: white;
    --default-layout-width: 1488px;
    --text-color: #393939;
    --hover-color: #e5e5e5;
    --base-size: 16px;
    --lh: 1.313;
    --fontmain: 'Helvetica', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans',
        sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
    --colormain: #0071ba;
    --colorsub: #bb8d09;
    --common-text-color: white;
    --common-btn-confirm: #185ba8;
    --common-btn-deny: #bb8d09;
    --common-error: #ff9600;
    --common-notify: greenyellow;
    --common-green: #00c087;
    --common-red: #d01a1d;
    --common-white: #fff;
    --common-orange: #ffac42;
    --border: #ccc;
    --text-in-light: #181818;

    --input-focus: #ffac42;

    --footer-container: #f5f5f5;
    --footer-text: #262626;
    --footer-next-disable: #ccc;

    --bg-menu-color: #f5f5f5;
    --bg-scrollbar: #000;
    --bg-table-color: #f5f5f5;
    --box-shadow-color: #0000004d;

    // --dropdown-image: url('../assets/images/dropdown.svg');

    //date picker
    --date-picker-bg: --common-white;
    --date-picker-border: darken(#3f4458, 50%);
    --date-picker-arrow-hover: --common-orange;
    --date-picker-month-fg: #000;
    --date-picker-month-bg: --common-white;
    --date-picker-weekdays-fg: #000;
    --date-picker-weekdays-bg: transparent;
    --date-picker-day-fg: #000;
    --date-picker-day-hover-bg: darken(--common-white, 25%);
    --date-picker-today-border: --common-orange;
    --date-picker-today-bg: --common-orange;
    --date-picker-today-fg: #000;
    --date-picker-day-selected-fg: --common-white;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;
    height: 100%;
    z-index: 0;
}

body {
    font-family: 'Roboto', sans-serif;
    font-size: 1.6rem;
    line-height: 1.5;
    text-rendering: optimizeSpeed;
    color: var(--text-color);
}

button,
input,
[tabindex] {
    outline: none;
    border: none;
}

a[href] {
    color: var(--text-color);
    text-decoration: none;
}

li {
    text-decoration: none;
    list-style-type: none;
}

.sm-text {
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 400;
    line-height: 1.4;
    font-size: 1.2rem;
}

.lg-text {
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
    line-height: 1.2;
    letter-spacing: -0.02rem;
    font-size: 1.6rem;
}

.header-title {
    margin-top: 100px;
    font-family: SuisseWorks;
    font-size: 32px;
    font-weight: 700;
    letter-spacing: -0.5px;
    line-height: 40px;
}

.title {
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    font-size: calc(var(--base-size) + 4px);
    margin-top: 15px;
    color: var(--colormain);
}
.content {
    // margin: 15px 30px;
    box-shadow: 2px 2px 4px 0 var(--box-shadow-color);
    -webkit-box-shadow: 2px 2px 4px 0 var(--box-shadow-color);
}
